.form {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.form label {
  font-size: 20px;
  margin-bottom: 5px;
  color: #000;
}

.form label input {
  display: flex;
  justify-content: center;
  width: 275px;
  height: 20px;
  border: 2px solid teal;
  border-radius: 5px;
}
.form h2 {
  font-size: 24px;
  color: teal;
}

.form button {
  text-align: center;
  width: 100px;
  height: 30px;
  color: teal;
  background-color: #fff;
  border-radius: 5px;
}

.form button:hover {
  color: #fff;
  background-color: teal;
}


